import React, { useEffect, useState} from "react";
import {
  Cell, BarChart, Bar, XAxis, YAxis, Tooltip,
} from 'recharts';
import {COLORS} from "./Colors";

import {
    MDBDataTable
} from "mdbreact";

function ShowBlock ( {shows, stats} ) {
  const [showChartData, setShowChartData] = useState([]);

    const showColumns = [
      {
        label: '',
        field: 'name',
        sort: 'disabled'
      },
      {
        label: '',
        field: 'status',
        sort: 'asc'
      },
      {
        label: '',
        field: 'score',
        sort: 'asc'
      },
      {
        label: '',
        field: 'review',
        sort: 'asc'
      }
    ];

  const [showTableData, setShowTableData] = useState({
      columns: showColumns,
      rows: [
      ]
  });

    useEffect(() =>{
            if (shows)
            {
              setShowTableData({
                columns: showColumns,
                rows: shows.map((show) => {
                    return {
                            name: <a className="dataTable" href={window.location.origin + '/show/' + show.show.tmdb_id}>{show.show.tmdb_name}</a>,
                            name2: show.show.tmdb_name,
                            status: show.status,
                            score: show.score,
                            review: show.review,
                            /*clickEvent: (e) => {
                                //window.open('/game/' + game.game.rawg_slug);
                                history.push('/movie/' + movie.movie.tmdb_id)
                            }*/
                        }      
                  })
              });
            }
        },
        // eslint-disable-next-line react-hooks/exhaustive-deps
        [shows]
    );

    useEffect(() =>{
      setShowChartData([]);
      if (stats)
      {
        if (stats?.genres){
            let newData = [];
            for (let genre in stats.genres)
              if (stats.genres[genre].spent_time_percent > 2)
                newData.push({name: stats.genres[genre].name, '': stats.genres[genre].spent_time_percent});
            newData = newData.sort((a, b) => a[''] > b[''] ? -1 : 1);
            setShowChartData(newData);
        }
      }},
        // eslint-disable-next-line react-hooks/exhaustive-deps
        [stats]
      );

    

    return(
        <div>
            <p>  : {stats?.count},  : {stats?.total_spent_time}</p>  
            <MDBDataTable
                striped
                bordered
                small
                data={showTableData}
                info={false}
                barReverse={true}
                noBottomColumns={true}
                noRecordsFoundLabel="  !"
                paginationLabel={["", ""]}
                entriesLabel="   "
                searchLabel=''
                />
              <div hidden={showChartData.length < 1}>
                <BarChart width={1000} height={300}
                    data={showChartData}
                    margin={{ top: 5, right: 10, left: 10, bottom: 20, }}
                >
                  <XAxis dataKey="name" tickLine={false} tick={{ fill: "rgb(238, 238, 238)"}} interval={0} angle={-10} tickMargin={15}/>
                  <YAxis domain={[0, 'dataMax']} tick={{ fill: "rgb(238, 238, 238)"}}/>
                  <Tooltip itemStyle={{color: 'rgb(238, 238, 238)', backgroundColor: 'rgb(30, 30, 30)'}}
                    contentStyle={{color: 'rgb(238, 238, 238)', backgroundColor: 'rgb(30, 30, 30)', borderRadius: "10px"}}
                    cursor={false}/>
                  <Bar dataKey="">
                      {
                          showChartData.map((entry, index) => <Cell fill={COLORS[index]} key={index}/>)
                      }
                  </Bar>
                </BarChart>
            </div>
        </div>  
    )
}

export default ShowBlock;
